.drop--down{
    width: min(100%,400px);
    height: inherit;
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: start;
}

.drop--down label
{
    line-height: 250%;

}

.drop--down--list--dispaly{
    width: 100%;
    height: 120px;
    padding: 1rem;
    position: absolute;
    line-height: 200%;
    background-color: rgb(245, 245, 245);
    margin-top: 10%;
    font-size: 11px !important;
    overflow:scroll;
    overflow-x:hidden;
    border-radius: 7px ;
    border: 1.4px solid rgb(196, 243, 243);
    box-shadow: 2px 6px 5px -2px rgba(0,0,0,0.75);
    text-transform: uppercase;
    font-family: poppins;
    margin-top: 50px;
    z-index: 1000;
    font-weight: 600;
    display: flex;
    flex-direction: column;
    align-items: start;
    line-height: 200%;
}

.drop--down--list--dispaly span{
    font-size: 8.5px;
    line-height: 260%;
    font-weight: 600;
    font-family: poppins;
} 

