.drop--down--list--dispaly span:hover{
    cursor: pointer;
    color: var(--bg--header--menu);
}

.drop--down--input--icon{

    width: 100%;
    display: flex;
    align-items: center;
    position: relative;
    padding: 0.1 0.1rem;
    border: 1.6px solid rgb(17, 222, 222);
    border-radius: 5px;
}

.drop--down--input--icon input{
    border: none;
}

.drop--down--input--icon span{
    position: absolute;
    right: 10px;
}

input:hover{
    cursor: pointer;
 
}

.select_full{
    width: min(100%,400px);
}

.select_medium{
    width: min(100%,300px);
}

.select_small{
    width: min(100%,190px);
}

@media screen and (max-width: 620px) {

    .sel{
        font-size: 14px;
    }
}

@media screen and (max-width: 420px) {

    .sel{
        font-size: 13px;
    }

    .drop--down--list--dispaly
    {
        margin-top: 19%;
    }

}